'use strict'

//scroll eventListener Ìï®Ïàò Îì±Î°ù
function changeScrollY(){
  document.addEventListener('scroll',()=>{
    changeNavbarColor(window.scrollY);
  });
}

// scrollYÍ∞Ä navbarÏùò ÎÜíÏù¥Î≥¥Îã§ Ïª§ÏßÄÎäî Í≤ΩÏö∞ navbar ÏÉâ Î≥ÄÌôîÏãúÏºúÏ£ºÎäî Ìï®Ïàò
function changeNavbarColor(scrollY){  
  const navbar = document.querySelector('#navbar');
  const navbarItems = document.querySelector('.navbar__menu');
  const navbarheight = navbar.getBoundingClientRect().height;

  if(scrollY>navbarheight){
    navbar.style.backgroundColor = '#000000';
    navbarItems.style.backgroundColor = '#000000';
  }else {
    navbar.style.backgroundColor = '#24292E';
    navbarItems.style.backgroundColor = '#24292E';
  }
}

//NavbarÏùò navÎ™©Î°ùÏù¥ ÌÅ¥Î¶≠ÎêòÏóàÏùÑÎïå Íµ¨ÌòÑÎêòÎäî ÏΩúÎ∞±Ìï®Ïàò
function onClickNavbarItems(){
  const navbarMenus = document.querySelector('.navbar__menu');
  navbarMenus.addEventListener('click',(e)=>{
    goCorrectPage(e);
  });
}

//ÏõêÌïòÎäî ÌéòÏù¥ÏßÄÎ°ú scrollÏùÑ Ïù¥ÎèôÌï¥Ï£ºÎäî Ìï®Ïàò
function goCorrectPage(e){
  const target = e.target;
  const link = target.dataset.link;

  if(link == null){
    return;
  }
  
  let type = 'start';
  const width = document.body.getBoundingClientRect().width;

//ÌôîÎ©¥ ÎÑàÎπÑÏóê Îî∞Îùº Îã§Î•∏ scrollintoView Ï°∞Í±¥ Î∂ÄÏó¨
    if(link == '#about'){
      if(width>768){
        type = 'center'
      }else{
        type = 'start';
      }
    }else if(link == '#skills'){
      type = 'start';
    }else if(link == '#home'){
      document.querySelector(link).scrollIntoView(false);
      return;
    }else{
      type = 'start';
    }
      document.querySelector(link).scrollIntoView({block:type});
}

// ÌôîÎ©¥ ÎÑàÎπÑ 768px Ïù¥ÌïòÏù∏ Í≤ΩÏö∞ navbar toggle Î≤ÑÌäº ÏÇ¨Ïö©
let navbarToggle = false;

function onClickToggleMenu(){

  const toggleMenu = document.querySelector('.navbar__toggle-btn');
  const navbarMenu = document.querySelector('.navbar__menu');

  toggleMenu.addEventListener('click',()=>{
    navbarMenu.classList.toggle('open');
  }); 
}


function init(){
  changeScrollY(); // Ïä§ÌÅ¨Î°§ Ïù¥Î≤§Ìä∏ Îì±Î°ù
  onClickNavbarItems(); // nav ÏïÑÏù¥ÌÖú ÌÅ¥Î¶≠ Îì±Î°ù
  onClickToggleMenu(); // toggle Î©îÎâ¥ Í∏∞Îä• Íµ¨ÌòÑ(Î∞òÏùëÌòï)
}

init();
